.HireMe {
  padding: 1rem;
  margin: 3rem auto 1rem auto;
  text-align: center;
  border-radius: .5rem;
}

body.dark .HireMe{
  background-color: #444;
  border: 1px dotted #aaa;
}

body.light .HireMe{
  background-color: #ddd;
  border: 1px dotted #ccc;
}

.HireMe-more {
  text-align: left;
  margin: 0 4.5rem;
}

.HireMe-morehint {
  cursor: pointer;
  margin-top: .5rem;
  text-align:center;
}

.HireMe-alert {
  font-weight: 600;
}


